<template>
  <div>
    <div>Admin Page</div>

    <ul>
      <li
        class="m-1 flex justify-center"
        v-for="(v, k) in lsItems"
        v-bind:key="k"
      >
        <div class="p-1 bg-black text-white text-xs">{{ k }}</div>
        <div class="pl-2">{{ lsGet(k) }}</div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      isAdmin: false,
    };
  },
  mounted() {
    this.isAdmin = this.$store.getters.userUid == 2;
  },
  computed: {
    lsItems() {
      const items = {};
      for (let i = 0; i < localStorage.length; i++) {
        const k = localStorage.key(i);
        const v = localStorage.getItem(k);
        items[k] = v;
      }

      const name_uid = `${this.$store.getters.userName} (${this.$store.getters.userUid})`;

      items["MODE"] = process.env.NODE_ENV;
      items["MODE"] = name_uid;
      items["API-URL"] = this.$store.getApiUrl();
      items["LS-TOKEN"] = localStorage.getItem("loginToken");

      return items;
    },
  },
  methods: {
    lsGet(key) {
      return this.lsItems[key];
    },
  },
};
</script>
